.Loading {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

.movies .movies__movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.movies__movie h3,
.movies__movie h5 {
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  position: relative;
  border-radius: 5px;
  color: #adaead;
}

.movies__movie img {
  position: relative;
  top: -15px;
  max-width: 120px;
  margin-right: 30px;
}

.movies__movie .movie__title,
.movies__movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movies__movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movies__movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  padding-left: 20px;
  margin: 5px 0px;
}

.movie__genres .genres__genre li {
  margin-right: 10px;
  font-size: 14px;
}
